// Variables
$mobile-height: 400px;
$tablet-height: 500px;
$desktop-height: 900px;

$tablet-breakpoint: 768px;
$desktop-breakpoint: 1024px;
$small-tablet-breakpoint: 900px;
$mobile-breakpoint: 480px;

// Navbar Styles
.navbar {
  .nav-link {
    font-size: 1.2rem;
  }
}

//Hero Section
.front-page-hero {
  margin-top: -16px;
}

.hero-section-large {
  // Base styles
  height: $mobile-height;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  background-color: blue;

  // Overlay
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }

  // Background image
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
  }

  // Layout Builder overrides
  .layout-builder-block {
    color: #333;
    padding: 1rem;

    .hero-title,
    .hero_sub_content {
      color: #333 !important;
    }
  }

  // Responsive heights
  @media (min-width: $tablet-breakpoint) {
    height: $tablet-height;
  }

  @media (min-width: $desktop-breakpoint) {
    height: $desktop-height;
  }
}

// Hero Content
.hero-content {
  position: relative;
  z-index: 2;

  .hero-title {
    font-size: 5rem;
    margin-bottom: 1rem;

    // Responsive adjustments
    @media (max-width: $small-tablet-breakpoint) {
      font-size: 3rem !important;
    }

    @media (max-width: $mobile-breakpoint) {
      font-size: 2rem;
    }
  }

  .hero-sub-content {
    font-size: 1.5rem !important;
    margin: 1rem auto !important;
    max-width: 800px;
    overflow-wrap: break-word;
    padding: 10px;

    // Responsive adjustments
    @media (max-width: $small-tablet-breakpoint) {
      font-size: 1rem !important;
    }

    @media (max-width: $mobile-breakpoint) {
      font-size: 0.5rem;
    }
  }

  .btn-primary {
    font-size: 1.25rem;
    padding: 1rem 1.75rem;
    background-color: $primary;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;

    &:hover {
      background-color: darken($primary, 10%);
    }
  }
}

// Miscellaneous
.bid-copyright {
  font-size: 1.2rem;
  margin-top: 15px;
}

.alert-success {
  z-index: 99999;
}

.nav.nav-tabs.mb-3 {
    margin-bottom: -20px !important;
}

/* Targets the Contact link specifically */
a.nav-link[data-drupal-link-system-path="contact"] {
    font-size: 1.2rem;
}


@media (max-width: 767px) {
    .bid-copyright{ 
        margin-top: 8px !important;
  }
}
